.search {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  background-color: #02c8ae; }

.form {
  width: 80%;
  margin: 0 auto;
  display: flex; }

.form__container {
  display: flex;
  border-radius: 5px;
  margin: 0;
  padding: 0;
  background-color: #fff; }

.dropdown {
  width: 100%;
  margin: 0 auto; }

.btn:hover {
  background-color: #edf1ee; }

.btn:active {
  border-radius: 5px 0 0 0; }

.dropdown-menu {
  width: auto;
  display: flex;
  flex-direction: column;
  position: absolute;
  background-color: #fff;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  font-size: 70%;
  color: #003669;
  margin: 0;
  padding: 0; }

.dropdown-menu {
  display: none; }

.form__dropdown-item {
  width: 100%;
  display: block;
  margin: 0;
  padding: 10px 20px; }

.form__dropdown-item:hover {
  background-color: #243d33;
  color: #fff; }

.btn {
  display: block;
  width: 150px;
  height: 50px;
  border: none;
  border-right: 1px solid #ececec;
  background-color: #fff;
  margin: 0;
  padding: 0;
  text-align: center;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  color: #003669;
  font-size: 90%; }

.dropdown__btn-contries {
  width: 200px; }

.btn:hover .dropdown-menu {
  display: flex; }

.form__submit {
  display: block;
  width: 100%;
  height: 50px;
  border: none;
  border: 1px solid #62FFD1;
  border-radius: 0 5px 5px 0;
  background-color: #62FFD1;
  margin: 0;
  padding: 10px 20px;
  text-align: center;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  color: #003669;
  font-size: 90%; }

.form__submit:hover {
  border: 1px solid #4ae4b6;
  background-color: #4ae4b6; }

.dropdown__btn-from {
  width: 150px;
  border: 1px solid #fff;
  border-right: 1px solid #ececec;
  border-radius: 5px 0 0 5px; }

.dropdown__btn-tourists {
  border: none; }

.dropdown__btn span {
  display: none; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body,
html {
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.body__wrapper {
  width: 100%;
  margin: 0 auto;
  background-color: #e8f8f1; }

@media (max-width: 768px) {
  .body__wrapper {
    width: 100%;
    margin: 0 auto; } }

@media (max-width: 320px) {
  .body__wrapper {
    width: 100%;
    margin: 0 auto; } }

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: .67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace,monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b, strong {
  font-weight: bolder; }

code, kbd, samp {
  font-family: monospace,monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -.25em; }

sup {
  top: -.5em; }

img {
  border-style: none; }

button, input, optgroup, select, textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button, input {
  overflow: visible; }

button, select {
  text-transform: none; }

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: .35em .75em .625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

.countries ul {
  list-style: none;
  margin: 0;
  margin-top: 10px;
  padding: 0; }

.countries .checkbox input {
  display: none; }

.countries .checkbox {
  position: relative;
  margin-left: 30px; }

.countries .checkbox label,
.countries .checkbox div {
  margin-left: 10px;
  margin-bottom: 10px; }

.countries .checkbox::before {
  position: absolute;
  content: "";
  top: 0;
  left: -20px;
  width: 23px;
  height: 23px;
  border: 1px solid #a0e4c2;
  border-radius: 5px; }

.checkbox:active::before {
  position: absolute;
  content: "";
  top: 0;
  left: -20px;
  width: 23px;
  height: 23px;
  border: 1px solid #a0e4c2;
  background-image: url(../img/checkbox-mark.svg);
  border-radius: 5px; }

.result {
  width: 100%;
  max-width: 1920px;
  display: flex;
  margin: 0 auto;
  padding: 0;
  justify-content: space-around; }

.filter {
  width: 30%;
  max-width: 1920px;
  display: flex;
  margin: 0 auto;
  margin-top: 2%;
  padding: 0; }

.filter__container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 0 0 5px #889caf; }

.filter__title {
  display: block;
  position: relative;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  font-size: 100%;
  color: #003669;
  text-transform: uppercase;
  text-align: right;
  margin: 0;
  padding: 0;
  padding-right: 5%;
  padding-bottom: 5%;
  margin-top: 6%;
  margin-bottom: 2%; }

.filter__title::before {
  position: absolute;
  content: "";
  width: 30px;
  height: 27px;
  top: 0;
  left: 10px;
  background-image: url(../img/filter.svg); }

.filter__line {
  width: 100%;
  height: 2px;
  border-bottom: 1px solid #003669;
  box-shadow: 0 1px 2px #889caf;
  margin: 0;
  margin-bottom: 5%;
  padding: 0; }

.filter__form {
  display: flex;
  flex-direction: column; }

.filter__container-search {
  width: 100%;
  margin: 0 auto;
  display: flex;
  margin: 0 auto;
  padding: 0;
  margin-bottom: 10%;
  position: relative; }

.filter__search {
  width: 90%;
  height: 210%;
  margin: 0 auto;
  border: none;
  border-radius: 3px;
  border-bottom: 3px solid #b6c5d3;
  box-shadow: 1px 2px 4px #b6c5d3; }

.filter__search::placeholder {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  font-size: 80%;
  color: #003669;
  padding-left: 20px; }

.filter__container-search::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  top: 10px;
  right: 25px;
  background-image: url(../img/search.svg);
  z-index: 10; }
